<route>
{
  "meta": {
     "title": "Review Item"
  }
}
</route>
<template>

  <div>

    <data-loader :handler="loadItem">
      <template v-slot:loading>
        <v-card>
          <v-skeleton-loader type="article,actions"></v-skeleton-loader>
        </v-card>
      </template>
      <template v-slot:default="{ data: { review } = { } }">
        <v-card>
          <v-card-text>
            {{ review }}
          </v-card-text>
        </v-card>
      </template>
    </data-loader>

  </div>

</template>

<script>

export default {
  name: 'QueueItem',
  components: {
  },

  data: () => ({
    dialog: true
  }),

  methods: {
    loadItem () {
      console.log(this.$route)
      return this.$store.dispatch('api/request', { apiGetReviewItem: { params: { ...this.$route.params } } })
    }
  }
}
</script>
